import React, { useCallback, CSSProperties, useEffect } from 'react';
import { throttle } from 'lodash-es';
import { AddHeadNormal, ArrowRightNormal, MemberNormalNormal } from '@ali/we-design';
import { loadImg } from '@ali/4ever-utils';
import paragraphFactory, { BaseParagraphProps } from '../paragraphFactory';
import preventDefaultOnClick from '../../uitls/preventDefaultOnClick';
import { LoadingWrapper } from '../loadingWarpper';
import { CommonMouseEvent, ILocaleProps, IOrganizerModel, ReceiverTypeEnum, CalendarType } from '../../type';
import { zhLocale } from '../../locale';
import {
  AddReceiverIconWrapper,
  AuxiliaryLoadingWrapper,
  AvatarWrapper,
  ContainerWapper,
  EllipsisWrapper,
  NameWrapper,
  ReceiverWrapper,
  SquareAvatarWrapper,
  SquareTextAvatarWrapper,
  TipContentWrapper,
  TipTextWrapper,
  TipWrapper,
  TotalBodyWapper,
  TotalIconWapper,
  TypeName,
} from './styled';
import { DEFAULT_ICON_STYLE, RECEIVER_MARGIN } from '../../constants';
import getTextWithoutEmoji from '../../uitls/getTextWithoutEmoji';

const SQUAREAVATAR_SIZE_STYLE: CSSProperties = { width: '20px', height: '20px' };

// 检查是否有中文
const HAS_CHINESE_REG = RegExp('[\\u4E00-\\u9FFF]+', 'g');

interface IProps extends BaseParagraphProps {
  locale?: ILocaleProps;
  openProfile?: (uid: number) => void;
  jumpToCalendar?: CommonMouseEvent;
  isLoading?: boolean;
  isReadonly?: boolean;
}

interface ReceiverProps {
  uid: number;
  name?: string;
  avatarMediaId?: string;// 头像需要经过处理
  isReceiverPlaceholder?: boolean;
  type: ReceiverTypeEnum;//  ####0:创建者，1:已接收；当前mock全部是已接收
  organizer?: IOrganizerModel[];
  locale?: ILocaleProps;
  openProfile?: (uid: number) => void;
  isReadonly?: boolean;
  mode?: CalendarType;
}

export const TotalWarpper: React.FC<{
  text: string;
  isReadonly?: boolean;
  jumpToCalendar?: CommonMouseEvent;
  mode?: CalendarType;
}> = (props) => {
  const { text, isReadonly, jumpToCalendar, mode } = props;
  const isShanhui = mode === CalendarType.SHANHUI;
  return (
    <ReceiverWrapper
      isReadonly={isReadonly}
      onMouseDown={jumpToCalendar}
      onClick={preventDefaultOnClick}
      backgroundColor={isShanhui ? 'transparent' : ''}
      textHoverColor={isShanhui ? 'rgba(49, 126, 208, 1)' : ''}
    >
      <TotalBodyWapper margin={isShanhui ? '0' : ''}>
        <NameWrapper>{text}</NameWrapper>
        <TotalIconWapper>
          <ArrowRightNormal />
        </TotalIconWapper>
      </TotalBodyWapper>
    </ReceiverWrapper>
  );
};

const SquareAvatarImage: React.FC<{ url: string; title?: string }> = (props) => {
  const { url } = props;
  const [status, setStatus] = React.useState<'loading' | 'loaded'>('loading');

  const handleLoad = useCallback(() => {
    setStatus('loaded');
  }, []);

  return (
    <React.Fragment>
      <SquareAvatarWrapper style={SQUAREAVATAR_SIZE_STYLE} {...props} />
      {status === 'loading' ? <AuxiliaryLoadingWrapper
        src={url}
        data-type="avatar"
        data-status={status}
        onLoad={handleLoad}
        onError={handleLoad}
      /> : null}
    </React.Fragment>
  );
};

const SquareAvatar: React.FC<{ url?: string; title?: string }> = (props) => {
  const { url, title } = props;
  const [urlEnable, setUrlEnable] = React.useState(!!url);
  useEffect(() => {
    if (url) {
      loadImg(url).then(() => {
        setUrlEnable(true);
      }).catch(() => {
        setUrlEnable(false);
      });
    }
  }, [url]);
  if (url && urlEnable) {
    return (<SquareAvatarImage url={url} title={title} />);
  }
  const titles = Array.from(props.title || '');
  return (
    <SquareTextAvatarWrapper>
      <span>{titles[titles.length - 1]}</span>
    </SquareTextAvatarWrapper>
  );
};

export const ReceiverComponent: React.FC<ReceiverProps> = React.memo((props) => {
  const {
    isReadonly,
    isReceiverPlaceholder,
    locale,
    type,
    name = '',
    uid,
    avatarMediaId,
    organizer = [],
    openProfile,
    mode,
  } = props;
  let typeName = (locale || {})[ReceiverTypeEnum[type]?.toString()] || '';
  // TODO：解决国际化tips宽度不一致问题，tips的宽度按照中文的字符数来计算
  let chineseTypeName = (zhLocale || {})[ReceiverTypeEnum[type]?.toString()] || '';
  if (organizer.some((org) => org.uid === uid) && locale) {
    typeName = locale.organizer;
    chineseTypeName = zhLocale.organizer;
  }
  // 防止双击重复弹出profile问题
  const openTargetProfile = useCallback(throttle(() => {
    if (!openProfile) return;
    openProfile(uid);
  }, 350), [uid, openProfile]);

  const onMouseDown = useCallback((event: React.MouseEvent<Element>) => {
    event.preventDefault();
    event.stopPropagation();
    openTargetProfile();
  }, [openTargetProfile]);

  const withoutEmoji = getTextWithoutEmoji(name);
  // 如果该用户没有头像信息，这里是移除姓取名 与端上保持一致
  const showName = withoutEmoji.substr(1) || withoutEmoji;
  // 中文不限制宽度，非中文限制 tip 宽度
  const tipWidth = !typeName || HAS_CHINESE_REG.test(typeName) ? 'auto' : `${chineseTypeName.length * 1.1}em`;
  const avatarNode = isReceiverPlaceholder ?
    (<AddReceiverIconWrapper><AddHeadNormal /></AddReceiverIconWrapper>)
    : (<SquareAvatar url={avatarMediaId} title={showName} />);

  if (mode === CalendarType.SHANHUI) {
    return (
      <ReceiverWrapper
        data-testid="calendar-receiver"
        isReadonly={isReadonly}
        onMouseDown={onMouseDown}
        onClick={preventDefaultOnClick}
        backgroundColor="transparent"
        textHoverColor="rgba(49, 126, 208, 1)"
        style={{ marginRight: 6 }}
      >
        {!isReceiverPlaceholder &&
          <AvatarWrapper>
            {avatarNode}
          </AvatarWrapper>}
        <NameWrapper style={{ paddingLeft: isReceiverPlaceholder ? 0 : 4 }}>
          <EllipsisWrapper>{name}{(!isReceiverPlaceholder && typeName) ? <TypeName>({typeName})</TypeName> : null}</EllipsisWrapper>
        </NameWrapper>
      </ReceiverWrapper>
    );
  }

  return (
    <ReceiverWrapper
      data-testid="calendar-receiver"
      isReadonly={isReadonly}
      onMouseDown={onMouseDown}
      onClick={preventDefaultOnClick}
    >
      <AvatarWrapper>
        {avatarNode}
      </AvatarWrapper>
      <NameWrapper>
        <EllipsisWrapper>{name}</EllipsisWrapper>
      </NameWrapper>
      <TipWrapper>
        {typeName ? (
          <TipContentWrapper>
            <TipTextWrapper style={{ width: tipWidth }}>{typeName}</TipTextWrapper>
          </TipContentWrapper>
        ) : null}
      </TipWrapper>
    </ReceiverWrapper>
  );
});

export const ReceiversParagraph: React.FC<IProps> = React.memo((props) => {
  const { metadata, openProfile, locale, isLoading, isReadonly, jumpToCalendar } = props;
  const { topReceivers = [], organizer, totalCount, mode } = metadata || {};
  const Paragraph = paragraphFactory(
    <MemberNormalNormal style={{ ...DEFAULT_ICON_STYLE, marginTop: '3px' }} />,
    { marginBottom: `-${RECEIVER_MARGIN}px` },
  );
  const totleCountText = (locale?.receiverCount || '').replace('{count}', String(totalCount));
  const totleNode = totalCount && totalCount > topReceivers.length ?
    (<TotalWarpper text={totleCountText} isReadonly={isReadonly} jumpToCalendar={jumpToCalendar} mode={mode} />)
    : null;
  if (isLoading && topReceivers.length === 0) {
    return (
      <Paragraph>
        <LoadingWrapper widthRange="98%" />
      </Paragraph>
    );
  }
  return (
    <Paragraph>
      <ContainerWapper>
        {topReceivers.map((rec) => {
          return (
            <ReceiverComponent
              key={rec.uid || rec.name}
              {...rec}
              isReadonly={isReadonly}
              openProfile={openProfile}
              locale={locale}
              organizer={organizer}
              mode={mode}
            />
          );
        })}
        {totleNode}
      </ContainerWapper>
    </Paragraph>
  );
});
